import React, { useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core";
import { useLocation } from "react-router-dom";
import { CSSTransition } from "react-transition-group";
import Introduction from "./Introduction";
import Works from "./Works";
import Labs from "./Labs";
import Courses from "./Courses";
import Articles from "./Articles";
import "./styles.css";

const useStyles = makeStyles((theme) => ({
  menu: {
    flex: 3,
    padding: 32,
  },
  introduction: {
    color: theme.palette.text.primary,
    fontSize: 20,
    fontWeight: "bold",
    whiteSpace: "pre-line",
  },
  email: {
    color: theme.palette.text.hint,
    marginTop: 8,
    marginBottom: 64,
  },
  option: {
    color: theme.palette.text.hint,
    fontSize: 24,
    fontWeight: "bold",
    marginTop: 16,
    marginBottom: 16,
    cursor: "pointer",
    textDecoration: "none",
    display: "block",
  },
  optionSelected: {
    color: theme.palette.text.primary,
    fontSize: 24,
    fontWeight: "bold",
    marginTop: 16,
    marginBottom: 16,
    cursor: "pointer",
    textDecoration: "none",
    display: "block",
  },
}));

const Content: React.FC = () => {
  const classes = useStyles();
  let location = useLocation();
  const [introductionPath, setIntroductionPath] = useState(false);
  const [worksPath, setWorksPath] = useState(false);
  const [labsPath, setLabsPath] = useState(false);
  const [coursesPath, setCoursesPath] = useState(false);
  const [articlesPath, setArticlesPath] = useState(false);

  useEffect(() => {
    setIntroductionPath(false);
    setWorksPath(false);
    setLabsPath(false);
    setCoursesPath(false);
    setArticlesPath(false);
    setTimeout(() => {
      switch (location.pathname) {
        case "/":
          setIntroductionPath(true);
          break;
        case "/introduction":
          setIntroductionPath(true);
          break;
        case "/works":
          setWorksPath(true);
          break;
        case "/labs":
          setLabsPath(true);
          break;
        case "/courses":
          setCoursesPath(true);
          break;
        case "/articles":
          setArticlesPath(true);
          break;
      }
    }, 1000);
  }, [location.pathname]);

  return (
    <div className={classes.menu}>
      <CSSTransition
        in={introductionPath}
        timeout={1000}
        classNames="alert"
        unmountOnExit
      >
        <Introduction />
      </CSSTransition>
      <CSSTransition
        in={worksPath}
        timeout={1000}
        classNames="alert"
        unmountOnExit
      >
        <Works />
      </CSSTransition>
      <CSSTransition
        in={labsPath}
        timeout={1000}
        classNames="alert"
        unmountOnExit
      >
        <Labs />
      </CSSTransition>
      <CSSTransition
        in={coursesPath}
        timeout={1000}
        classNames="alert"
        unmountOnExit
      >
        <Courses />
      </CSSTransition>
      <CSSTransition
        in={articlesPath}
        timeout={1000}
        classNames="alert"
        unmountOnExit
      >
        <Articles />
      </CSSTransition>
    </div>
  );
};

export default Content;
