import React from "react";
import { makeStyles } from "@material-ui/core";
import WordSliding from "../assets/wordSliding.gif";
import WordBlurry from "../assets/wordBlurry.gif";
import "./styles.css";

function openInNewTab(href: string) {
  Object.assign(document.createElement("a"), {
    target: "_blank",
    href,
  }).click();
}

const useStyles = makeStyles((theme) => ({
  block: {
    backgroundColor: "#F2A640",
    width: 300,
    borderRadius: 8,
    cursor: "pointer",
    marginRight: 16,
  },
}));

const Labs: React.FC = () => {
  const classes = useStyles();
  return (
    <div>
      <img
        alt={"文字滑入"}
        className={classes.block}
        src={WordSliding}
        onClick={() => {
          openInNewTab("https://codepen.io/darrenyaoyaoyao/pen/vYNoYxO");
        }}
      />
      <img
        alt={"文字模糊"}
        className={classes.block}
        src={WordBlurry}
        onClick={() => {
          openInNewTab("https://codepen.io/darrenyaoyaoyao/pen/MWadwVQ");
        }}
      />
    </div>
  );
};

export default Labs;
