import React from "react";
import { makeStyles } from "@material-ui/core";
import MilkJobs from "../assets/milkjobs.jpeg";
import MusicBot from "../assets/musicBot.png";
import Bomberman from "../assets/bomberman.jpg";
import "./styles.css";

function openInNewTab(href: string) {
  Object.assign(document.createElement("a"), {
    target: "_blank",
    href,
  }).click();
}

const useStyles = makeStyles((theme) => ({
  container: {
    display: "flex",
    flexDirection: "row",
    flexWrap: "wrap",
    alignItems: "flex-start",
  },
  block: {
    backgroundColor: "#F2A640",
    width: 300,
    maxWidth: "100%",
    height: "auto",
    borderRadius: 8,
    cursor: "pointer",
    margin: 16,
  },
}));

const Works: React.FC = () => {
  const classes = useStyles();
  return (
    <div className={classes.container}>
      <img
        alt={"牛奶找工作"}
        className={classes.block}
        src={MilkJobs}
        onClick={() => {
          openInNewTab("https://milk.jobs");
        }}
      />
      <img
        alt={"音樂聊天機器人"}
        className={classes.block}
        src={MusicBot}
        onClick={() => {
          openInNewTab("https://yvchen.github.io/MusicBot2/");
        }}
      />
      <img
        alt={"音樂聊天機器人"}
        className={classes.block}
        src={Bomberman}
        onClick={() => {
          openInNewTab(
            "https://www.youtube.com/watch?v=dAczqIxOxcI&feature=youtu.be"
          );
        }}
      />
    </div>
  );
};

export default Works;
